<template>
  <div class="account-orders">
    <NotifyModal
      :show="showNotifyModal"
      :product-sku="notifyModalProduct && notifyModalProduct.sku"
      @close="closeNotifyModal"
    />
    <BModal
      id="modal"
      v-model="outOfStockProductsModal"
      size="md"
      content-class="no-product-stock"
      title="Product(en) die niet op voorraad zijn"
      centered
      @hide="
        outOfStockProductsList = [];
        outOfStockProductsModal = false;
      "
      :hide-footer="true"
      ><div class="no-product-wrap">
        <div
          class="no-product-box"
          v-for="product in outOfStockProductsList"
          :key="product.product_sku"
        >
          <img :src="product.image.medium" />
          <div class="info-popup">
            <h2>{{ product.product_name }}</h2>
            <p class="my-price">
              {{ formatCurrency(product.product_sale_price.value) }}
            </p>
          </div>
        </div>
      </div>
    </BModal>
    <div
      class="address-block"
      v-if="showOrders"
      :class="{ 'hide-tab-content': isTabShow && isMobileScreen }"
    >
      <h1>{{ $t("my_orders") }}</h1>
      <div class="back-link" v-if="isMobileScreen" @click="$emit('showTabs')">
        <i class="lnr lnr-chevron-left"></i>
        <span>{{ $t("go_back") }}</span>
      </div>

      <div class="address-table-wrap">
        <h3 v-if="ordersList && ordersList.length === 0">
          {{ $t("no_orders_available") }}
        </h3>
        <template v-if="ordersList && ordersList.length > 0">
          <BTable
            id="order-table"
            class="address-table"
            responsive
            :items="ordersList"
            :fields="fields"
            :per-page="paginationConf.perPage"
            :current-page="paginationConf.currentPageNr"
            v-if="!isMobileScreen"
          >
            <template v-slot:cell(action)="data">
              <div class="order-btn-action" :class="{ 'only-view': !allowedReorder }">
                <BLink class="c-btn c-btn-primary" @click="viewOrder(data.item)">{{
                  $t("view_order")
                }}</BLink
                ><span v-if="allowedReorder">&nbsp;|&nbsp;</span>
                <BLink
                  v-if="allowedReorder"
                  class="c-btn c-btn-green"
                  @click="orderAgain(data.item)"
                >
                  {{ $t("order_again") }}</BLink
                >
              </div>
            </template>
          </BTable>
          <div class="mobile-order" v-if="isMobileScreen">
            <div class="mo-inner">
              <div class="mo-title">
                <label>{{ $t("order_detail") }}</label>
                <label>{{ $t("action") }}</label>
              </div>
              <div class="mo-info">
                <ul>
                  <li
                    v-for="product in ordersList"
                    :key="product.product_sku"
                    :per-page="paginationConf.perPage"
                    :current-page="paginationConf.currentPageNr"
                  >
                    <div class="text">
                      <label>{{ product.number }}</label>
                      <p>{{ convertDate(product.order_date) }}</p>
                      <p>
                        {{
                          product.billing_address &&
                          product.billing_address.firstname + " " + product.billing_address.lastname
                        }}
                      </p>
                      <p>
                        {{ formatCurrency(product.total.base_grand_total.value) }}
                      </p>
                      <span>{{ product.status }}</span>
                    </div>
                    <div class="action">
                      <BLink class="c-btn c-btn-primary" @click="viewOrder(product)">{{
                        $t("view_order")
                      }}</BLink>
                      <BLink
                        v-if="allowedReorder"
                        class="c-btn c-btn-green"
                        @click="orderAgain(product)"
                        >{{ $t("order_again") }}</BLink
                      >
                    </div>
                  </li>
                </ul>
              </div>
            </div>
          </div>

          <div class="cst-pagination-bar">
            <label>
              Pagina
              <span>{{ paginationConf.currentPageNr }}</span> van
              <span>{{ Math.ceil(rows / paginationConf.perPage) }}</span>
            </label>
            <BPagination
              :model-value="paginationConf.currentPageNr"
              :total-rows="rows"
              :per-page="paginationConf.perPage"
              class="cst-pagination"
              aria-controls="order-table"
              @update:model-value="tmp"
            >
              <template #page="{ page }">
                <BLink class="page-link" @click.prevent="tmp(page)">
                  {{ page }}
                </BLink>
              </template>
            </BPagination>
          </div>

          <div class="pagination-bar-down">
            <div class="left">
              <div class="display-drop">
                <label>Weergave:</label>
                <select v-model.number="paginationConf.perPage">
                  <option value="1">1</option>
                  <option value="9">9</option>
                  <option value="24">24</option>
                  <option value="48">48</option>
                  <option value="72">72</option>
                  <option value="98">98</option>
                  <option value="120">120</option>
                </select>
              </div>
            </div>
          </div>
        </template>
      </div>
    </div>
    <div class="order-view" v-if="!showOrders && orderDetail">
      <div class="title">
        <h2>{{ $t("order") + " " + orderDetail.number }}</h2>
        <p>{{ $t("order_detail") }}</p>
      </div>
      <div class="order-view-inner">
        <div class="order-view-wrap">
          <div
            class="single-product"
            v-for="product in orderDetail.items"
            :key="product.product_sku"
          >
            <div class="img-wrap">
              <div class="img" :style="`background-image: url('${product.image.medium}');`"></div>
            </div>
            <div class="product-info">
              <div class="product-name">{{ product.product_name }}</div>
              <div class="extra-info">
                <div>{{ $t("number") }}: {{ product.quantity_ordered }}</div>
              </div>
              <div class="stock-info">
                <label
                  v-if="product.product"
                  class="stock-status status"
                  :class="product.product.stock_status == 'IN_STOCK' ? 'in-stock' : 'out-stock'"
                >
                  {{ $t(product.product.stock_status == "IN_STOCK" ? "In Stock" : "Out of Stock") }}
                </label>
                <div class="price">
                  {{ formatCurrency(product.product_sale_price.value) }}
                </div>
              </div>

              <div v-if="allowedReorder">
                <div
                  v-if="product.product && product.product.stock_status == 'OUT_OF_STOCK'"
                  class="cart-btn notify-order"
                  @click="addToNotify(product.product)"
                  key="notify-btn"
                >
                  <i class="fas fa-envelope"></i>
                </div>

                <div v-else class="order-again-btn" @click.prevent="orderProductAgain(product)">
                  <b-link class="c-btn c-btn-green">
                    {{ $t("order_again") }}
                  </b-link>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="common-part">
          <div class="address">
            <div class="top">
              <div class="date">
                {{ $t("order_date") }}:
                <span>{{ convertDate(orderDetail.order_date) }}</span>
              </div>
              <div class="status">
                {{ $t("status") }}:
                <span>{{ orderDetail.status }}</span>
              </div>
            </div>
            <div class="address-info" v-if="orderDetail.billing_address">
              <div>
                {{ orderDetail.billing_address.firstname }}
                {{ orderDetail.billing_address.lastname }}
              </div>
              <div>{{ orderDetail.billing_address.street[0] }}</div>
              <div>
                {{ orderDetail.billing_address.postcode }}
                {{ orderDetail.billing_address.city }}
              </div>
              <div class="mo-num">
                {{ orderDetail.billing_address.telephone }}
              </div>
            </div>
          </div>
          <div class="grand-total">
            <ul>
              <li>
                <label>{{ $t("total_shipping_cart") }}:</label>
                <div>
                  <p>{{ formatCurrency(orderDetail.total.subtotal.value) }}</p>
                </div>
              </li>
              <li>
                <label>{{ $t("postage_cost") }}:</label>
                <div>
                  <p>
                    {{ formatCurrency(orderDetail.total.total_shipping.value) }}
                  </p>
                </div>
              </li>
              <li>
                <label>{{ $t("VAT") }}:</label>
                <div>
                  <p>{{ formatCurrency(orderDetail.total.total_tax.value) }}</p>
                </div>
              </li>
            </ul>
            <div class="final-price">
              <div class="final-total">
                <label>{{ $t("total_btw") }}:</label>
                <div>
                  <p>
                    {{ formatCurrency(orderDetail.total.base_grand_total.value) }}
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
        <BLink @click="viewOrderList" class="back-link">
          <i class="lnr lnr-chevron-left"></i>
          {{ $t("back") }}
        </BLink>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, reactive, computed, onMounted, watch, onUnmounted } from "vue";
import config from "@config";
import axios from "axios";
import { useStore } from "vuex";
import { useI18n } from "vue-i18n";
import { useRouter } from "vue-router";
import { Logger } from "@storefront/core/lib/logger";
import { getCurrentLanguage } from "@storefront/core/i18n";
import { useWindowSize } from "@storefront/core/composable/useWindowSize";
import NotifyModal from "@/base/core/components/common/NotifyModal.vue";
import { getCustomerCartToken } from "@storefront/core/data-resolver/cart";

defineProps({
  isTabShow: {
    type: Boolean,
  },
});

const store = useStore();
const router = useRouter();
const windowSize = useWindowSize();
const { t } = useI18n();

const isMobileScreen = ref(windowSize.width <= 768);
const showOrders = ref(true);
const orderDetail = ref(null);
const showNotifyModal = ref(false);
const notifyModalProduct = ref(null);
const outOfStockProductsModal = ref(false);
const outOfStockProductsList = ref([]);
const paginationConf = reactive({
  currentPageNr: 1,
  perPage: 9,
});
// const options = ref([10, 20, 30]);
const fields = ref([
  {
    key: "number",
    label: t("order_number"),
  },
  {
    key: "order_date",
    label: t("order_date"),
    formatter: (value) => convertDate(value),
  },
  {
    key: "billing_address",
    label: t("send_to"),
    formatter: (value) => value.firstname + " " + value.lastname,
  },
  {
    key: "price",
    label: t("grand_total"),
    formatter: (value, key, item) => {
      return formatCurrency(item.total.base_grand_total.value);
    },
  },
  {
    key: "status",
    label: t("status"),
  },
  {
    key: "action",
    label: t("action"),
  },
]);

const ordersList = computed(() => {
  const orders = store.getters["user/getMyOrders"];
  orders.forEach((element, index) => {
    orders[index].status = t(element.status);
  });
  orders.sort((a, b) => (a.order_date > b.order_date ? -1 : 1));
  return orders;
});

const cartItems = computed(() => store.getters["cart/getCartItems"]);
const activeTab = computed(() => store.getters["user/getAccountTabIndex"]);
const rows = computed(() => ordersList.value.length);
const allowedReorder = computed(() => config.allow_reorder);

function formatCurrency(value) {
  const formatter = new Intl.NumberFormat("nl-NL", {
    style: "currency",
    currency: "EUR",
  });
  return formatter.format(value);
}

function convertDate(date) {
  let reviewDate = new Date(date);
  let dd = reviewDate.getDate();
  let mm = reviewDate.getMonth() + 1;
  let yyyy = reviewDate.getFullYear();
  if (dd < 10) dd = "0" + dd;
  if (mm < 10) mm = "0" + mm;
  reviewDate = `${dd}-${mm}-${yyyy}`;
  return reviewDate;
}

function tmp(id) {
  // showOrders.value = false;
  // setTimeout(() => {
  // showOrders.value = true;
  paginationConf.currentPageNr = id;
  // }, 0.001);
}

function viewOrder(data) {
  if (Array.isArray(data.items) && data.items[0]) {
    showOrders.value = false;
    orderDetail.value = data;
  } else {
    const msg = {
      type: "danger",
      title: t("view_order"),
      text: t("details_not_found"),
    };
    store.dispatch("messages/sendMessage", { message: msg });
  }
}

function viewOrderList() {
  showOrders.value = true;
  orderDetail.value = null;
  window.scrollTo({
    top: 0,
    behavior: "smooth",
  });
}

function addToNotify(product) {
  notifyModalProduct.value = product;
  showNotifyModal.value = true;
}

function closeNotifyModal() {
  notifyModalProduct.value = null;
  showNotifyModal.value = false;
}

async function orderProductAgain(product) {
  if (product.product) {
    if (!product.product.backorders_allowed) {
      if (typeof product.product.stock_qty != "undefined") {
        let quantity = 1;
        const existingProduct = cartItems.value.find(
          (item) => product.product_sku == item.product.sku
        );
        if (existingProduct) {
          quantity += existingProduct.quantity;
        }
        if (quantity > product.product.stock_qty) {
          const msg = {
            type: "danger",
            title: t("shopping_basket"),
            text: t("product_not_in_stock_msg"),
          };
          store.dispatch("messages/sendMessage", { message: msg });
          return false;
        }
      }
    }
  }

  if (product.product_type === "simple") {
    const items = `[ { sku: "${product.product_sku}" quantity: 1 } ]`;

    await addToCart(items);
  } else if (product.product_type === "bundle") {
    if (product.bundle_options != null) {
      const selected_options = [];
      product.bundle_options.forEach((element) => {
        selected_options.push(element.values[0].uid);
      });

      const items = `[{
        quantity: 1,
        sku: "${product.product_sku}",
        selected_options: ${JSON.stringify(selected_options)}
      }]`;

      await addToCart(items);
    } else {
      router.push(product.product_url_key);
    }
  } else {
    router.push(product.product_url_key);
  }
}

async function orderAgain(order) {
  const lang = getCurrentLanguage();
  const storelang = config.languages[lang];
  const storeview = storelang.storeview;

  const headers = {
    "Content-Type": "application/json",
    Authorization: `Bearer ${store.getters["user/getUserToken"]}`,
    Store: storeview,
  };

  const query = `mutation {
    reorderItems(orderNumber: "${order.number}") {
      cart {
        id
        items {
          uid
          product { sku }
          quantity
          prices { price { value } }
        }
      }
      userInputErrors { code message path }
    }
  }`;

  const retval = await axios({
    url: config.shop.graphQLURL,
    method: "POST",
    headers: headers,
    data: { query: query },
  }).catch((e) => {
    Logger.error("reOrder", "Account orders", e)();
    throw e;
  });

  if (retval.data.data.reorderItems) {
    if (retval.data.data.reorderItems.userInputErrors.length === 0) {
      if (!store.getters["cart/getCartServerToken"] && !localStorage.getItem("cartServerToken")) {
        const newCartToken = await getCustomerCartToken(store);
        store.commit("cart/setServerToken", newCartToken);
      }

      await store.dispatch("cart/loadCart", { store });

      const msg = {
        type: "success",
        title: t("shopping_basket"),
        text: t("added_product"),
      };
      store.dispatch("messages/sendMessage", { message: msg });
    } else {
      const errors = retval.data.data.reorderItems.userInputErrors;
      errors.forEach((element) => {
        const msg = {
          type: "danger",
          title: t("shopping_basket"),
          text: element.message,
        };
        store.dispatch("messages/sendMessage", { message: msg });
      });
      store.dispatch("cart/loadCart", { store });
    }
  }
}

async function addToCart(items) {
  const retval = await store.dispatch("cart/addProductToCart", {
    items,
    store,
  });

  if (retval === false) {
    const msg = {
      type: "danger",
      title: t("shopping_basket"),
      text: t("not_add_product"),
    };
    store.dispatch("messages/sendMessage", { message: msg });
  } else {
    const msg = {
      type: "success",
      title: t("shopping_basket"),
      text: t("added_product"),
    };
    store.dispatch("messages/sendMessage", { message: msg });
  }

  store.dispatch("cart/loadCart", { store });
}

function updateScreenSize() {
  isMobileScreen.value = window.innerWidth <= 768;
}

watch(activeTab, () => {
  showOrders.value = true;
});

onMounted(() => {
  store.dispatch("user/loadOrders", { page: 1, perPage: 10, store });

  // Listen to window resize
  window.addEventListener("resize", updateScreenSize);
});

onUnmounted(() => {
  // Cleanup the event listener
  window.removeEventListener("resize", updateScreenSize);
});
</script>
