<template>
  <div class="product-card--holder">
    <a href="#" class="lnr lnr-cross add-to-wishlist" @click.prevent="removeItem(product.id)"></a>
    <router-link :to="`/${product.product.url_key}`" class="product-card">
      <div v-if="product.product.image.large != null">
        <VueLazyBackgroundImage
          :image-source="product.product.image.large"
          image-class="product-card--img-top"
          errorImage="@/base/assets/logo.jpg"
          loadingImage="@/base/assets/logo.jpg"
          style="background-size: contain"
        >
          <div class="actionbuttons">
            <span class="sale-txt" v-if="onSale">sale</span>
            <span class="sale-txt" v-if="onNew">new</span>
          </div>
        </VueLazyBackgroundImage>
      </div>
      <div class="product-card--product-name">{{ product.product.name }}</div>

      <span class="d-block product-card--price"
        >&euro;{{
          product.product.price_range.minimum_price.final_price.value.toFixed(2).replace(".", ",")
        }}</span
      >
    </router-link>
  </div>
</template>

<script setup>
import { computed } from "vue";
import VueLazyBackgroundImage from "@/base/core/components/core/VueLazyBackgroundImage";
import { useStore } from "vuex";

defineProps({
  product: {
    type: Object,
    required: true,
  },
});

const store = useStore();

const onSale = computed(() => false);
const onNew = computed(() => false);

const removeItem = (id) => {
  store.dispatch("user/removeProductWishlist", { id, store });
};
</script>
