<template>
  <div class="account-newsletter">
    <div>
      <div class="account-address-book">
        <h1>{{ $t("newsletter_subscriptions") }}</h1>
        <div class="address-block">
          <h2>{{ $t("newsletter_subscription_option") }}</h2>
          <BRow>
            <BCol sm="12">
              <BFormCheckbox
                id="checkbox-1"
                name="checkbox-1"
                label-for="checkbox1"
                v-model="isSubscribed"
                >{{ $t("newsletter_subscription_selected") }}</BFormCheckbox
              >
            </BCol>
          </BRow>
        </div>

        <div class="form-footer">
          <BButton
            :disabled="isSaveBtnDisabled"
            @click="save"
            variant="info"
            class="text-white form-submit-btn"
            >{{ $t("save") }}</BButton
          >
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, computed, onMounted, watch } from "vue";
import { useStore } from "vuex";

const store = useStore();
const isSaveBtnDisabled = ref(false);

const isSubscribed = ref(false);
const user = computed(() => store.getters["user/getCurrentUser"]);

onMounted(() => {
  isSubscribed.value = user.value.is_subscribed;
});

// const tabIndex = computed({
//   get() {
//     return store.getters["user/getAccountTabIndex"];
//   },
//   set(val) {
//     store.commit("user/setAccountTabIndex", val);
//   },
// });

const save = async () => {
  isSaveBtnDisabled.value = true;
  await store.dispatch("user/updateUserNewsletter", {
    is_subscribed: isSubscribed.value,
    store,
  });
  isSaveBtnDisabled.value = false;
};

watch(
  () => user.value.is_subscribed,
  () => {
    isSubscribed.value = user.value.is_subscribed;
  }
);
</script>
