<template>
  <BaseLayout v-slot="{}">
    <Breadcrumbs />
    <BContainer class="account">
      <BTabs
        pills
        card
        vertical
        v-model="tabIndex"
        class="flex-column"
        :nav-wrapper-class="{
          'cst-tab w-100': true,
          sidebarhide: !isTabShow && isMobileScreen,
        }"
      >
        <BTab @click="hideTabs" :title="$t('my_account')">
          <AccountMyAccount @showTabs="showTabs" :isTabShow="isTabShow" />
        </BTab>
        <BTab @click="hideTabs" :title="$t('my_orders')">
          <BCardText>
            <AccountOrders @showTabs="showTabs" :isTabShow="isTabShow" />
          </BCardText>
        </BTab>
        <BTab @click="hideTabs" :title="$t('my_wishlist')">
          <BCardText>
            <AccountWishlist @showTabs="showTabs" :isTabShow="isTabShow" />
          </BCardText>
        </BTab>
        <BTab @click="hideTabs" :title="$t('address_book')">
          <AccountAddressBook @showTabs="showTabs" :isTabShow="isTabShow" />
        </BTab>
        <BTab @click="hideTabs" :title="$t('account_information')">
          <AccountInformation @showTabs="showTabs" :isTabShow="isTabShow" />
        </BTab>
        <BTab @click="hideTabs" :title="$t('change_email')">
          <AccountEmail @showTabs="showTabs" :isTabShow="isTabShow" />
        </BTab>
        <BTab @click="hideTabs" :title="$t('change_password')">
          <AccountPassword @showTabs="showTabs" :isTabShow="isTabShow" />
        </BTab>
        <!-- <BTab @click="hideTabs" :title="$t('my_product_reviews')">
          <b-card-text><AccountReviews /></b-card-text>
        </BTab> -->
        <BTab @click="hideTabs" :title="$t('newsletter_subscriptions')">
          <NewsletterSubscription @showTabs="showTabs" :isTabShow="isTabShow" />
        </BTab>
      </BTabs>
    </BContainer>
  </BaseLayout>
</template>

<script setup>
import { ref, computed, onMounted } from "vue";
import { useStore } from "vuex";
import { useRouter } from "vue-router";
import { useI18n } from "vue-i18n";
import { isMobile } from "mobile-device-detect";

import BaseLayout from "@/base/core/components/common/BaseLayout";
import Breadcrumbs from "@/base/core/components/Breadcrumbs";
import AccountInformation from "@/base/core/components/account/AccountInformation";
import AccountEmail from "@/base/core/components/account/AccountEmail";
import AccountPassword from "@/base/core/components/account/AccountPassword";
import AccountMyAccount from "@/base/core/components/account/AccountMyAccount";
import AccountAddressBook from "@/base/core/components/account/AccountAddressBook";
import NewsletterSubscription from "@/base/core/components/account/NewsletterSubscription";
import AccountWishlist from "@/base/core/components/account/AccountWishlist";
import AccountOrders from "@/base/core/components/account/AccountOrders";

const store = useStore();
const router = useRouter();
const { t } = useI18n();

const isTabShow = ref(true);
const thanksActive = ref(false);

const isLoggedIn = computed(() => store.getters["user/getIsLoggedIn"]);

const tabIndex = computed({
  get: () => store.getters["user/getAccountTabIndex"],
  set: (val) => store.commit("user/setAccountTabIndex", val),
});

const isMobileScreen = computed(() => isMobile);

const hideTabs = () => {
  isTabShow.value = false;
};

const showTabs = () => {
  isTabShow.value = true;
};

const goLogout = () => {
  store.dispatch("user/logout");
  router.push("/");
};

const formSubmit = () => {
  thanksActive.value = true;
};

// Lifecycle hook
onMounted(() => {
  const bcrumb = { current: t("account"), routes: [] };
  store.commit("breadcrumbs/set", bcrumb);
});
</script>
