<template>
  <div class="account-informate">
    <h1>{{ $t("change_account_info") }}</h1>
    <BRow>
      <BCol md="6">
        <BFormGroup
          class="account-inputs"
          id="first-name-group"
          :label="$t('form_first_name') + '*'"
          label-for="first_name"
        >
          <BFormInput
            id="first_name"
            v-model="form.firstname"
            type="text"
            v-on:blur="validateFirstName()"
            :state="form.firstnameState"
            aria-describedby="firstname-feedback"
          ></BFormInput>
          <BFormInvalidFeedback id="firstname-feedback">
            {{ form.firstnameError }}
          </BFormInvalidFeedback>
        </BFormGroup>
        <BFormGroup
          class="account-inputs"
          id="last-name-group"
          :label="$t('form_last_name') + '*'"
          label-for="last_name"
        >
          <BFormInput
            id="last_name"
            v-model="form.lastname"
            type="text"
            v-on:blur="validateLastName()"
            :state="form.lastnameState"
            aria-describedby="lastname-feedback"
          ></BFormInput>
          <BFormInvalidFeedback id="lastname-feedback">
            {{ form.lastnameError }}
          </BFormInvalidFeedback>
        </BFormGroup>
      </BCol>
    </BRow>
    <div class="form-footer">
      <BButton @click="submit" variant="info" class="text-white form-submit-btn"
        >Opslaan</BButton
      >
    </div>
  </div>
</template>

<script setup>
import { ref, computed, watch, onMounted } from "vue";
import { useStore } from "vuex";
import { useI18n } from "vue-i18n";

const { t } = useI18n();
const store = useStore();

const form = ref({
  firstname: "",
  firstnameState: null,
  firstnameError: "",
  lastname: "",
  lastnameState: null,
  lastnameError: "",
});

const user = computed(() => store.getters["user/getCurrentUser"]);

watch(
  user,
  () => {
    form.value.firstname = user.value.firstname;
    form.value.lastname = user.value.lastname;
  },
  { immediate: true }
);

onMounted(() => {
  form.value.firstname = user.value.firstname;
  form.value.lastname = user.value.lastname;
});

const validateFirstName = () => {
  const trimmedFirstName = form.value.firstname.trim();
  if (trimmedFirstName == "") {
    form.value.firstnameState = false;
    form.value.firstnameError = t("firstname_required");
  } else if (trimmedFirstName.length > 40) {
    form.value.firstnameState = false;
    form.value.firstnameError = t("firstname_too_long");
  } else {
    form.value.firstnameState = true;
    form.value.firstnameError = "";
  }
};

const validateLastName = () => {
  const trimmedLastName = form.value.lastname.trim();
  if (trimmedLastName == "") {
    form.value.lastnameState = false;
    form.value.lastnameError = t("lastname_required");
  } else if (trimmedLastName.length > 40) {
    form.value.lastnameState = false;
    form.value.lastnameError = t("lastname_too_long");
  } else {
    form.value.lastnameState = true;
    form.value.lastnameError = "";
  }
};

const submit = async () => {
  let formValid = true;
  if (form.value.firstnameState != true) {
    validateFirstName();
    if (form.value.firstnameState != true) {
      formValid = false;
    }
  }
  if (form.value.lastnameState != true) {
    validateLastName();
    if (form.value.lastnameState != true) {
      formValid = false;
    }
  }
  if (formValid == true) {
    const retval = await store.dispatch("user/updateAccount", {
      account: form.value,
      store,
    });
    if (retval == true) {
      const msg = {
        type: "success",
        title: t("account_modify"),
        text: t("account_modified"),
      };
      store.dispatch("messages/sendMessage", { message: msg });
    } else {
      const msg = {
        type: "danger",
        title: t("account_modify"),
        text: t("account_not_modified"),
      };
      store.dispatch("messages/sendMessage", { message: msg });
    }
  }
};
</script>
