<template>
  <div class="account-my-account">
    <h1>{{ $t("my_account") }}</h1>
    <div class="address-block">
      <h2>{{ $t("account_information") }}</h2>
      <BRow>
        <BCol sm="6">
          <h3>{{ $t("contact_information") }}</h3>
          <address>
            <span>{{ user.firstname }} {{ user.lastname }}</span>
            <span>{{ user.email }}</span>
          </address>
          <BLink class="link" @click="goToEditAccount">{{ $t("edit") }}</BLink
          >&nbsp;&nbsp;&nbsp;|&nbsp;&nbsp;&nbsp;<BLink class="link" @click="goToEditPassword">{{
            $t("change_password")
          }}</BLink>
        </BCol>
        <BCol sm="6">
          <h3>{{ $t("newsletters") }}</h3>
          <BLink class="link" @click="goToEditSubscription">{{ $t("modify_newsletters") }}</BLink>
        </BCol>
      </BRow>
    </div>
    <div class="address-block">
      <h2>
        {{ $t("address_book") }} &nbsp;&nbsp;&nbsp;<b-link
          class="link"
          @click="goToManageAddress"
          >{{ $t("manage_addresses") }}</b-link
        >
      </h2>
      <BRow>
        <BCol sm="6">
          <h3>{{ $t("default_billing_address") }}</h3>
          <div v-if="defaultBilling == null">
            {{ $t("no_default_billing_address") }}
          </div>
          <address v-if="defaultBilling != null">
            <span>{{ defaultBilling.firstname }} {{ defaultBilling.lastname }}</span>
            <span v-if="defaultBilling.company != null">{{ defaultBilling.company }}</span>
            <span>{{ defaultBilling.street[0] }}</span>
            <span>{{ defaultBilling.city }}, {{ defaultBilling.postcode }}</span>
            <span>{{ defaultBilling.country_code }}</span>
            <span
              >T:<a :href="`tel:` + defaultBilling.telephone">
                {{ defaultBilling.telephone }}</a
              ></span
            >
            <span v-if="defaultBilling.vat_id != null">VAT:{{ defaultBilling.vat_id }}</span>
          </address>
          <BLink class="link" @click="goToEditAddress(defaultBilling?.id)">{{
            $t("edit_address")
          }}</BLink>
        </BCol>
        <BCol sm="6">
          <h3>{{ $t("default_shipping_address") }}</h3>
          <div v-if="defaultShipping == null">
            {{ $t("no_default_shipping_address") }}
          </div>
          <address v-if="defaultShipping != null">
            <span>{{ defaultShipping.firstname }} {{ defaultShipping.lastname }}</span>
            <span v-if="defaultShipping.company != null">{{ defaultShipping.company }}</span>
            <span>{{ defaultShipping.street[0] }}</span>
            <span>{{ defaultShipping.city }}, {{ defaultShipping.postcode }}</span>
            <span>{{ defaultShipping.country_code }}</span>
            <span
              >T:<a :href="`tel:` + defaultShipping.telephone">
                {{ defaultShipping.telephone }}</a
              ></span
            >
            <span v-if="defaultShipping.vat_id != null">VAT:{{ defaultShipping.vat_id }}</span>
          </address>
          <BLink class="link" @click="goToEditAddress(defaultShipping?.id)">{{
            $t("edit_address")
          }}</BLink>
        </BCol>
      </BRow>
    </div>
  </div>
</template>

<script setup>
import { computed } from "vue";
import { useStore } from "vuex";

const store = useStore();

// Computed properties
const tabIndex = computed({
  get: () => store.getters["user/getAccountTabIndex"],
  set: (val) => store.commit("user/setAccountTabIndex", val),
});

const user = computed(() => store.getters["user/getCurrentUser"]);
const defaultBilling = computed(() => store.getters["user/getDefaultBillingAddress"]);
const defaultShipping = computed(() => store.getters["user/getDefaultShippingAddress"]);

// Methods
function goToEditAccount() {
  tabIndex.value = 4;
}

function goToEditPassword() {
  tabIndex.value = 6;
}

function goToEditAddress(id = -1) {
  tabIndex.value = 3;
  store.commit("user/setEditAddressId", id);
}

function goToManageAddress() {
  tabIndex.value = 3;
  store.commit("user/setEditAddressId", 0);
}

function goToEditSubscription() {
  tabIndex.value = 7;
}
</script>
